<template>
    <el-container>
        <el-header>
            蓄水量模型管理
        </el-header>
        <el-main>
            <el-card shadow="hover">
                <el-form label-width="10%">
                    <el-form-item label="水库名称">
                        <el-select v-model="selectedReservoir"
                                   placeholder="未选择"
                                   @change="handleSelectedReservoirChange">
                            <el-option
                                    v-for="item in reservoirList"
                                    :label="item.name"
                                    :value="item.id"
                                    :key="item.id">
                                {{item.name}}
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
                <el-table v-show="selectedReservoir!==''" :data="models">
                    <el-table-column property="name" label="模型名称" width="150"
                                     align="center"></el-table-column>
                    <el-table-column label="参数（最左为0阶）">
                        <template slot-scope="scope">
                            {{getNumberStringFromDoubleString(scope.row.orders)}}
                        </template>
                    </el-table-column>
                    <el-table-column
                            fixed="right"
                            label="操作"
                            width="100"
                            align="center">
                        <template slot-scope="scope">
                            <el-button
                                    type="text"
                                    size="small"
                                    style="margin-right: 5px"
                                    @click="handleEditClick(scope.row)">编辑</el-button>
                            <el-popconfirm
                                    title="操作不可复原，确定删除吗？"
                                    icon="el-icon-info"
                                    iconColor="red"
                                    @onConfirm="handleDeleteClick(scope.row.id, scope.$index)"
                            >
                                <el-button slot="reference"
                                           type="text"
                                           size="small"
                                           :disabled="!deleteAble(scope.row.name)">
                                    删除
                                </el-button>
                            </el-popconfirm>

                        </template>
                    </el-table-column>
                </el-table>
                <el-button v-show="selectedReservoir!==''" style="margin-top: 1em;" @click="handleAddModelClick">添加模型</el-button>
            </el-card>

        </el-main>
        <el-dialog :title="modelEditorTitle" :visible.sync="modelEditor.visitable">
            <el-alert
                    v-show="!deleteAble(modelEditor.name)"
                    title="对系统内部蓄水量模型的修改，将在下次上传相关数据时丢失。"
                    type="warning"
                    style="margin-top: -30px; margin-bottom: 10px">
            </el-alert>
            <el-form :inline="true">
                <el-form-item label="名称" style="margin-left: 50px">
                    <el-input v-model="modelEditor.name" style="width: 190px;"
                              :disabled="!deleteAble(modelEditor.name)"/>
                </el-form-item>
            </el-form>
            <el-form label-width="15%">
                <el-form-item
                        v-for="(p, index) in modelEditor.params"
                        :label="index + '阶参数'"
                        :key="index"
                >
                    <el-input v-model="p.value"/>
                </el-form-item>
            </el-form>
            <el-button @click="handleEditAddParamClick">
                添加参数
            </el-button>
            <el-button @click="handleEditDeleteParamClick">删除最后一项参数</el-button>
            <el-button type="success" style="float:right;" v-show="!modelEditor.addMode" @click="handleEditSaveClick">
                保存
            </el-button>
            <el-button type="primary" style="float:right;" v-show="modelEditor.addMode" @click="handleAddModelSaveClick">
                新建
            </el-button>
        </el-dialog>
    </el-container>
</template>

<script>
    export default {
        name: "EditStorageModel",
        data: function () {
            return {
                reservoirList: [],
                selectedReservoir: '',
                models: [],
                modelEditor: {
                    id: -1,
                    visitable: false,
                    addMode: false,
                    name: '',
                    params: []
                }
            }
        },
        computed: {
            modelEditorTitle: function () {
                if (this.modelEditor.addMode) return '添加模型';
                else return '编辑模型';
            },
        },
        methods: {
            handleSelectedReservoirChange: function () {
                const self = this;
                this.models = [];
                this.axios.get('/api/model/getStorageModels',
                    {
                        params: {rid: this.selectedReservoir},
                    }).then((response) => {
                    if (response.status === 200) {
                        const data = response.data;
                        if ('' !== data) {
                            for (let i = 0; i < data.length; i++) {
                                self.models.push(data[i]);
                            }
                        }

                    } else {
                        console.log("error")
                    }
                });
            },
            handleEditClick: function (data) {
                this.modelEditor.addMode = false;
                this.modelEditor.id = data.id;
                this.modelEditor.name = data.name;
                this.modelEditor.params = [];
                const paramsArray = this.getArrayFromStringParams(data.orders);
                for (let i = 0; i < paramsArray.length; i++) {
                    this.modelEditor.params.push({value: paramsArray[i]});
                }
                this.modelEditor.visitable = true;
            },
            handleEditAddParamClick: function () {
                this.modelEditor.params.push({value: 0});
            },
            handleEditDeleteParamClick: function () {
                this.modelEditor.params.pop();
            },
            handleEditSaveClick: function () {
                let params = String();
                for (let i = this.modelEditor.params.length - 1, start = false; i >= 0; i--) {
                    if (this.modelEditor.params[i].value * 1 !== 0 || start) {
                        params = this.modelEditor.params[i].value + ',' + params;
                        start = true;
                    }

                }
                params = params.substring(0, params.lastIndexOf(','));
                let index = -1;
                for (let i = 0; i < this.models.length; i++) {
                    if (this.models[i].id === this.modelEditor.id) {
                        index = i;
                    }
                }
                this.axios.get('/api/model/setModel',
                    {
                        params: {
                            rid: this.selectedReservoir,
                            name: this.models[index].name,
                            newName: this.modelEditor.name,
                            sDate: +new Date(),
                            params: params,
                        },
                    }).then((response) => {
                    if (response.status === 200) {
                        if (response.data==='name'){
                            this.$notify({
                                title: '失败',
                                message: '发现名字冲突："' + this.modelEditor.name + '"',
                                type: 'warning'
                            });
                        }
                        else {
                            this.$set(this.models, index, {
                                id:this.modelEditor.id,
                                name: this.modelEditor.name,
                                orders: params,
                            });
                            this.modelEditor.visitable = false;
                            this.$notify({
                                title: '成功',
                                message: '模型"'+this.modelEditor.name+'"修改成功',
                                type: 'success'
                            });
                        }

                    } else {
                        console.log("error")
                    }
                });
            },
            handleDeleteClick: function (id, index) {
                this.axios.get('/api/model/deleteModel', {params: {id: id}}).then((response) => {
                    if (response.status === 200) {
                        this.models.splice(index, 1);
                        this.$notify({
                            title: '成功',
                            message: '已删除',
                            type: 'success'
                        });
                    }
                })
            },
            handleAddModelClick: function() {
                this.modelEditor = {
                    addMode: true,
                    id: -1,
                    name: '',
                    firstDate: '',
                    params: [{value:0}],
                    visitable: true,
                };
            },
            handleAddModelSaveClick: function(){
                let params = String();
                for (let i = this.modelEditor.params.length - 1, start = false; i >= 0; i--) {
                    if (this.modelEditor.params[i].value * 1 !== 0 || start) {
                        params = this.modelEditor.params[i].value + ',' + params;
                        start = true;
                    }

                }
                params = params.substring(0, params.lastIndexOf(','));
                this.axios.get('/api/model/addModel', {
                    params: {
                        rid: this.selectedReservoir,
                        name: this.modelEditor.name,
                        sDate: +new Date(),
                        type: 'storage',
                        params: params,
                    }
                }).then((response) => {
                    if (response.status === 200) {
                        if (response.data==='name'){
                            this.$notify({
                                title: '失败',
                                message: '发现名字冲突："' + this.modelEditor.name + '"',
                                type: 'warning'
                            });
                        }
                        else if (response.data === 'success'){
                            this.$notify({
                                title: '成功',
                                message: '"'+this.modelEditor.name+'"已添加至系统',
                                type: 'success'
                            });
                            this.handleSelectedReservoirChange();
                            this.modelEditor.visitable = false;
                        }

                    }
                })
            },
            getDateStringFromTimestamp: function (timestamp) {
                return new Date(timestamp).toLocaleDateString();
            },
            getArrayFromStringParams: function (str) {
                const strArr = str.split(',');
                const arr = [];
                for (let i = 0; i < strArr.length; i++) {
                    arr.push(strArr[i] * 1);
                }
                return arr;
            },
            getNumberStringFromDoubleString: function (str) {
                const arr = this.getArrayFromStringParams(str);
                let arrString = String();
                for (let i = 0; i < arr.length; i++) {
                    arrString = arrString.concat(arr[i], ', ');
                }
                return arrString.substring(0, arrString.length - 2);
            },
            deleteAble: function (name) {
                return [].indexOf(name) === -1;
            },
        },
        mounted() {
            this.axios.get('/api/reservoirInfo/reservoir').then((response) => {
                if (response.status === 200) {
                    this.reservoirList = response.data;
                    this.selectedReservoir = this.$store.getters.getReservoir;
                    if (''!==this.selectedReservoir){
                        for (let i = 0; i < this.reservoirList.length; i++){
                            if (this.reservoirList[i].name === this.selectedReservoir){
                                this.selectedReservoir = this.reservoirList[i].id;
                            }
                        }
                        this.handleSelectedReservoirChange();
                    }
                } else {
                    console.log("error")
                }
            });
        }
    }
</script>

<style scoped>
    .el-card {
        border: 1px solid #d4d4d4;
        width: 100%;
    }
</style>
